import React, { Component, useEffect, useState } from 'react';
import { Helmet } from "react-helmet";

import './careers.css';
import DocumentMeta from 'react-document-meta';

import PageBanner from '../../components/ui/page-banner/page-banner';
import SectionHeading from '../../components/pages-component/section-heading/section-heading';
import CareersGenericForm from './components/careers-generic-form/careers-generic-form';
import Modal from '../../components/ui/modal/modal';
import FormSuccessMsg from '../../components/pages-component/form-success-msg/form-success-msg'
import ShowMsg from '../../components/pages-component/showMSG/msg'
import postJobEnquiry from '../../services/api/post-job-enquiry';
import getJobs from '../../services/api/fetch-jobs'
import pubUnpubJobs from '../../services/api/publish-unpublish-job'
import deleteJob from '../../services/api/delete-job'
import { connect } from 'react-redux'

import * as utilityFunctions from '../../utility-functions/utility-functions';
import '../../components/pages-component/button/button.css'
import CreateCareer from './components/create-careers/careers-form'
import * as actionType from '../../store/actions/action-type';
import Career from './components/career/career'
import getCareerEnqReport from '../../services/api/get-career-report'
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';



const Careers = (props) => {

  const [formData, setformData] = useState(new FormData());
  const [fileSize, setfileSize] = useState(null);

  const [formValid, setformValid] = useState(false);
  const [errorMsg, seterrorMsg] = useState(null);
  const [formSubmissionStart, setformSubmissionStart] = useState(false);
  const [showModal, setshowModal] = useState(false);
  const [createCareer, setcreateCareer] = useState(false);
  const [updateCareer, setupdateCareer] = useState(false);
  const [careerId, setcareerId] = useState('');
  const [msg, setmsg] = useState(false);
  const [msgContent, setmsgContent] = useState('');
  const [attention, setattention] = useState(false);

  const onGetJob = (data) => {
    if (data.status === 'error') {
      data.data.response ? console.log(data.data.response) : console.log(data.data);
    } else {
      props.getJobs(data.data);
    }
  }

  useEffect(() => {
    window.scrollTo(0, 0);
    getJobs(onGetJob)
  },[])


  const onClearningFormData = () => {
    props.formData.delete('name');
    props.formData.delete('contact_number');
    props.formData.delete('email');
    // props.formData.delete('gender');
    props.formData.delete('position');
    props.formData.delete('resume');
    props.formData.delete('message');
  }

  const resetProperties = () => {
    props.setfileSize(null)
  }

  const onSubmitCallBack = (data) => {
    if (data.status === 201) {
      document.querySelector(".form--careers-generic-form").reset();
      onClearningFormData();
      resetProperties();
      utilityFunctions.clearSelectField('.select-input');
      setshowModal(true)
      setformValid(true)
      seterrorMsg(null)
      setformSubmissionStart(false)
    } else {
      if (data.response) {
        setformValid(false)
        seterrorMsg(data.response.data)
        setformSubmissionStart(false)
      } else {
        // const fileErrorMsg = { Resume: ["Max. limit for resume 5 MB"] };
        const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
        setformValid(false)
        // seterrorMsg(fileSize > 5 ? fileErrorMsg : genericErrorMsg)
        seterrorMsg(genericErrorMsg)
        setformSubmissionStart(false)
      }
    }
  }

  const onSubmitHandler = (event) => {
    event.preventDefault();
    setformSubmissionStart(true)
    postJobEnquiry(onSubmitCallBack, props.formData);
  }

  const onInputChange = (event) => {
    const inputFieldName = event.target.name;
    // console.log(inputFieldName);
    switch (event.target.type) {
      case 'text':
        if (inputFieldName === 'name') {
          props.formData.delete('name');
          props.formData.append('name', event.target.value);
        } else if (inputFieldName === 'contact_number') {
          const contactNo = event.target.value;
          props.formData.delete('contact_number');
          props.formData.append('contact_number', contactNo);
        } else if (inputFieldName === 'resume') {
          props.formData.delete('resume');
          props.formData.append('resume', event.target.value)
        }
        break;

      case 'email':
        if (inputFieldName === 'email') {
          props.formData.delete('email');
          props.formData.append('email', event.target.value);
        }
        break;

      case 'select-one':
        if (inputFieldName === 'gender') {
          if (event.target.value === 'Male') {
            props.formData.delete('gender');

            props.formData.append('gender', 'M');
          } else {
            props.formData.delete('gender');

            props.formData.append('gender', 'F');
          }
        } else if (inputFieldName === 'position') {
          props.formData.delete('position');

          props.formData.append('position', event.target.value);
        }
        break;

      case 'textarea':
        props.formData.delete('message');

        props.formData.append('message', event.target.value);
        break;

      default:
        break;
    }

    props.setformData(props.formData)

  }

  const createCareerModal = () => {
    setshowModal(true)
    setcreateCareer(true)
  }

  const updateCareer1 = (id) => {
    setshowModal(true)
    setcareerId(id)
    setupdateCareer(true)
  };

  const closeModal = () => {
    setshowModal(false)
    setcreateCareer(false)
    setupdateCareer(false)
    setcareerId('')
    setattention(false)
    setmsg(false)
    setmsgContent('')
    setformValid(false)
  };

  const unPublishCallback = (data) => {
    if (data.status === 200) {
      setmsg(true)
      setmsgContent('Successfully Changed')
      setshowModal(true)
      setattention(false)
    } else {
      setmsg(true)
      setmsgContent("Something went wrong")
      setshowModal(true)
      setattention(true)
    }
  };

  const publishJob = (id) => {
    const data = {
      id: id,
      publish: true
    }
    pubUnpubJobs(unPublishCallback, id, { publish: true })
    props.pubunpubJob(data)
  };

  const unpublishJob = (id) => {
    const data = {
      id: id,
      publish: false
    }
    pubUnpubJobs(unPublishCallback, id, { publish: false })
    props.pubunpubJob(data)
  };

  const deleteJobCallback = (data) => {
    if (data.status === 204) {
      setmsg(true)
      setmsgContent('Successfully Deleted')
      setshowModal(true)
      setattention(false)
    } else {
      console.log(data)
      setmsg(true)
      setmsgContent('Something Went Wrong')
      setshowModal(true)
      setattention(true)
    }
  };

  const deleteJob = (id) => {
    deleteJob(deleteJobCallback, id)
    props.deleteJob(id)
  };

  const getJobEnqReport = () => {
    getCareerEnqReport()
  }


  let flag = 0, noJob = 0;
  if (props.careerJobs) {
    flag = 1
    if (props.careerJobs.length < 1) {
      noJob = 1
    }
  }

  const meta = {
    title: 'Careers | Ethereal Machines | Bangalore, India',
    meta: {
      property: {
        'title': 'About Us | Ethereal Machines | Bangalore, India',
        'description': 'Ethereal Machines  Join us in building the next big thing.',
      }
    }
  };

  return (
    <React.Fragment>
      {createCareer || updateCareer
        ?
        <Modal show={showModal} clicked={closeModal}>
          <CreateCareer closeModal={closeModal} careerId={careerId} updateCareer={updateCareer1} />
        </Modal>
        :
        null
      }
      {
        formValid
          ?
          <Modal show={showModal} clicked={closeModal}>
            <FormSuccessMsg />
          </Modal>
          : null
      }
      {
        msg
          ?
          <Modal show={showModal} clicked={closeModal}>
            <ShowMsg msg={msgContent} attention={attention} />
          </Modal>
          : null
      }
      <div className="page page--careers">
        <Header />
        {/* <Helmet>
        <title>Careers at Ethereal Machines-Bangalore,India</title>
        <meta name="description" content="Ethereal Machines  Join us in building the next big thing."/>
        </Helmet> */}
        <DocumentMeta {...meta} />

        <PageBanner heading={"Careers in Ethereal Machines"} subHeading={"Make your dream job come true"} classValue={"page-banner--careers"} />
        <section className="section section--openings">
          <SectionHeading classValue={'u-margin-bottom-big u-text-center'} name={'Join us in Building the Next Big Thing'} />
          <div className="container">
            {
              props.session.session
                ?
                <div>
                  <button className="form-btn" onClick={createCareerModal}>Post a Job</button>
                  <button className="form-btn" onClick={getJobEnqReport}>Job Enquiry Report</button>
                </div>

                :
                null
            }
            {/* {
                flag === 1
                    ?
                    <div className="current-openings">
                      {
                        props.careerJobs.map((data) => {
                          return(
                              <Career 
                                  jobItems={data} key={data.id} 
                                  updateCareer={updateCareer1} 
                                  publishJob={publishJob} 
                                  unpublishJob={unpublishJob}
                                  deleteJob = {deleteJob}
                                  session = {props.session.session}
                              />
                          )
                        })
                      }
                    </div>
                    : null
              }
              {
                noJob === 1
                ?
                  <div className="no-openings">
                    <p className="u-text-center">Sorry, we have no current openings.</p>
                  </div>
                : null
              } */}
            <p className="u-text-center">We are happy you are thinking about a profession with Ethereal Machines, Bangalore, India! Let us join forces to put India on the world map. Together, we shall disrupt the Deep-Tech industry. Lets create the future leader of Deep-Tech from India.</p>
          </div>
        </section>
        <section className="section section--careers-form section-no-top-padding">
          <SectionHeading name={"Get in touch!"} classValue={"u-margin-bottom-big u-text-center"} hasSubHeading={true} subHeading={"Let's schedule an interview!"} />
          <div className="container">
            <CareersGenericForm
              onInputChange={onInputChange}
              onSubmitHandler={onSubmitHandler}
              formSubmissionStart={formSubmissionStart}
              errorMsg={errorMsg}
              showLoader={formSubmissionStart} />
          </div>
        </section>
        <Footer />
      </div>
    </React.Fragment>
  );
}

const mapStateToProps = (state) => {
  return {
    careerJobs: state.jobs.jobItems,
    session: state.login
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getJobs: (data) => {
      dispatch({
        type: actionType.FETCH_JOB,
        value: data
      });
    },
    pubunpubJob: (data) => {
      dispatch({
        type: actionType.UNPUBLISHED_JOB,
        value: data
      });
    },
    deleteJob: (data) => {
      dispatch({
        type: actionType.DELETE_JOB,
        value: data
      });
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Careers);
