import React, { Component, useEffect, useState } from 'react'
import FormControl from '../../../../components/pages-component/form-controls/components/form-control'
import '../../../../components/pages-component/button/button.css'
import postJob from '../../../../services/api/post-job'
import updateJob from '../../../../services/api/update-post-job'
import { connect } from 'react-redux'
import * as actionType from '../../../../store/actions/action-type';
import * as utilityFunctions from '../../../../utility-functions/utility-functions';
import ErrorBox from '../../../../components/pages-component/form/components/error-box'
import Loader from '../../../../components/ui/loader/loader'
import './career-form.css'
import TextEditor from '../../../text-editor/textEditor';
import uuid from 'uuid/'


const CreateCareer = (props) => {

    const [position, setposition] = useState('');
    const [experience, setexperience] = useState('');
    const [description, setdescription] = useState('');
    const [publish, setpublish] = useState(false);
    const [formValid, setformValid] = useState(true);
    const [errorMsg, seterrorMsg] = useState(null);
    const [id, setid] = useState(uuid());
    const [formSubmissionStart, setformSubmissionStart] = useState(false);

    useEffect((props) => {
        if (props.careerId) {
            const filterJobs = Object.values(props.jobItems)[1].filter((data) => data.id === props.careerId)
            setposition(filterJobs[0].position)
            setexperience(filterJobs[0].experience)
            setdescription(filterJobs[0].description)
            setpublish(filterJobs[0].publish)
            setid(filterJobs[0].id)
        }
    },[])

    const handleInputChange = (e) => {
        e.target.name(e.target.value)
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        name({ [name]: value, });
    }

    const handleInputChangedesc = (data) => {
        description(data)
    }

    const submitJob = (e) => {
        e.preventDefault();
        setformSubmissionStart(true)
        const data = createJob()
        if (props.updateCareer) {
            updateJob(submitCallBack, id, data)
        } else {
            if (data) {
                postJob(submitCallBack, data)
            }
        }
    };

    const submitCallBack = (data) => {
        let code = 201, careerData = createJob()
        if (props.updateCareer) {
            code = 200
        }
        if (data.status === code) {
            clearForm()
            props.closeModal()
            utilityFunctions.clearSelectField('.select-input');
            if (props.updateCareer) {
                props.onUpdateCareer(careerData)
            } else {
                props.createCareer(careerData)
            }
            setformValid(true)
            seterrorMsg(null)
            setformSubmissionStart(false)
        } else if (data.response) {
            setformValid(false)
            seterrorMsg(data.response.data)
            setformSubmissionStart(false)
        } else {
            const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
            setformValid(false)
            seterrorMsg(genericErrorMsg)
            setformSubmissionStart(false)
        }
    }

    const createJob = () => {
        const careerData = {
            position: position,
            experience: experience,
            description: description,
            publish: publish,
            id: id
        };
        return careerData
    };

    const clearForm = () => {
        setposition('')
        setdescription('')
        setexperience('')
        setpublish(false)
        setid('')
    };

    return (
        <form onSubmit={submitJob}>
            <FormControl>
                <label htmlFor="position">
                    Position
                        <span className="star">*</span>
                </label>
                <input
                    type="text"
                    name="position"
                    placeholder="Enter Position"
                    className="input-element"
                    id="position"
                    onChange={e => handleInputChange(e)}
                    value={position}
                />
            </FormControl>
            <FormControl>
                <label htmlFor="experience">
                    Experience
                        <span className="star">*</span>
                </label>
                <input
                    type="text"
                    name="experience"
                    placeholder="Enter Experience"
                    className="input-element"
                    id="experience"
                    onChange={e => handleInputChange(e)}
                    value={experience}
                />
            </FormControl>
            <label htmlFor="description">
                <b>Description</b>
                <span className="star">*</span>
            </label>
            <TextEditor
                placeholder="Enter the description"
                id="description"
                required={true}
                onChangeTextEditor={handleInputChangedesc}
                value={description}
            />
            <div style={{ margin: 5 }}>
                <input
                    type="checkbox"
                    name="publish"
                    id="publish"
                    onChange={e => handleInputChange(e)}
                    checked={publish}
                />{" "}<span><b>Publish</b></span>
            </div>
            { errorMsg ? <ErrorBox isRequired errorMsg={errorMsg} /> : null}
            { formSubmissionStart ? <Loader>Submitting your request...</Loader> : null}
            <button
                type="submit"
                className="form-btn"

                disabled={formSubmissionStart}
            >
                Submit
                </button>
        </form>
    )
}

const mapStateToProps = (state) => {
    return {
        jobItems: state.jobs
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createCareer: (data) => {
            dispatch({
                type: actionType.CREATE_JOB,
                value: data
            });
        },
        onUpdateCareer: (data) => {
            dispatch({
                type: actionType.UPDATE_JOB,
                value: data
            });
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateCareer)
